import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import { Observable, throwError, BehaviorSubject } from 'rxjs';
import {catchError, tap} from 'rxjs/operators';
import { JwtHelperService } from '@auth0/angular-jwt';
import {Router} from '@angular/router';
@Injectable()
export class AuthService{

  jwtHelper = new JwtHelperService();
  token: string;
  user: any;
  authenticated: boolean;

  userSubject: BehaviorSubject<any> = new BehaviorSubject(this.user);
  authenticatedSubject: BehaviorSubject<boolean> = new BehaviorSubject(null);

constructor(private http: HttpClient, private router: Router ) {
 this.token = localStorage.getItem('token');
 try {
    if(!this.token) { throw Error(); }
    this.user = this.jwtHelper.decodeToken(this.token);
  } catch (error) {
    this.user = {};
  }
 this.authenticated = Object.keys(this.user).length > 0;
 this.authenticatedSubject.next(this.authenticated);
 this.userSubject.next(this.user);
}




signIn(userCreds): Observable<any>{
  console.log(this.user);
  return this.http.post('/api/auth/signin', userCreds)
    .pipe(
      tap((res: any)=>{
        localStorage.setItem('token', res.token);
        const user = this.jwtHelper.decodeToken(res.token);
        this.userSubject.next(user);
        this.authenticatedSubject.next(Object.keys(user).length > 0);
        this.authenticated = true;
        this.token = res.token;
      }),
      catchError(err => throwError(err))
    );
}

signUp(userCreds): Observable<any>{
  return this.http.post('/api/auth/signup', userCreds)
    .pipe(
      catchError(err => throwError(err))
    );
}

  signOut(){
    localStorage.setItem('token', '');
    this.token = '';
    this.userSubject.next({});
    this.authenticatedSubject.next(false);
    this.authenticated = false
    this.router.navigate(['/'])
  }

}
