import {Component, OnInit} from '@angular/core'
import {FormBuilder, FormGroup, FormControl,  Validators, AbstractControl} from '@angular/forms'
import { AuthService } from '../Auth.Service';
import { Router } from '@angular/router';

@Component({
  selector: 'auth-signup',
  templateUrl: './Signup.component.html',
  styleUrls: ['./Signup.component.css']
})

export class SignupComponent implements OnInit{

  signupForm: FormGroup

  constructor(private fb: FormBuilder, private authService: AuthService, private router: Router){}

  ngOnInit(){
    this.signupForm = this.fb.group({
      username: ['', [Validators.minLength(3), Validators.required]],
      password: ['', [Validators.minLength(7), Validators.required]],
      confirmedPassword: ['']
    }, {validators: this.checkPasswords})
  }

  submitForm(){
    let usersCred = this.signupForm.value
    this.authService.signUp(usersCred).subscribe(res =>{
      this.router.navigate(['/signin'])
    }, err =>{
      if(err.error.message === "this username is already taken"){
        this.username.setErrors({usernameTaken: true})
      }
    })
  }

  get username(){
    return this.signupForm.get('username')
  }

  get password(){
    return this.signupForm.get('password')
  }

  get confirmedPassword(){
    return this.signupForm.get('confirmedPassword')
  }


  checkPasswords(group: FormGroup) { // here we have the 'passwords' group
  let password = group.controls.password.value;
  let confirmedPassword = group.controls.confirmedPassword.value;

  return password === confirmedPassword ? null : { notSame: true }
}
}


