import {Component, OnInit} from '@angular/core';
import {HttpClient} from '@angular/common/http'

@Component({
  selector: 'app-home',
  templateUrl: './Home.component.html',
  styleUrls: ['./Home.component.css']
})
export class HomeComponent implements OnInit{

  loadedPolls: boolean = false;
  copiedPolls: any;
  polls: any;
  private _filter: string;

  get filter(){
    return this._filter;
  }
  set filter(value) {
    this.filterPolls(value);
    this._filter = value;
  }

  constructor(private http: HttpClient){}


  ngOnInit(){
    this.http.get('/api/poll/getpolls').subscribe((res: any) =>{
      this.polls = res.polls;
      this.copiedPolls = this.polls;
      this.loadedPolls = true;
    })
  }

  removePoll(id){
    this.polls = this.polls.filter(poll => poll.id !== id);
    this.copiedPolls = this.copiedPolls.filter(poll => poll.id !== id);
  }



  filterPolls(filter){
    if(filter){
      this.copiedPolls = this.polls.filter(poll =>{
        return poll.poll_name.toLowerCase().includes(filter) || poll['created by'].toLowerCase().includes(filter)
      });
    }else{
      this.copiedPolls = this.polls;
    }
  }
}
