import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormArray, FormGroup, FormControl} from '@angular/forms';
import {HttpClient} from '@angular/common/http';
@Component({
  selector: 'app-create-poll',
  templateUrl: './create-poll.component.html',
  styleUrls: ['./create-poll.component.css']
})
export class CreatePollComponent implements OnInit {

  createPollForm: FormGroup;
  response: any;
  creatingPoll: boolean = false;

  constructor(private fb: FormBuilder, private http: HttpClient) { }

  ngOnInit() {
    this.createPollForm = this.fb.group({
      name: '',
      choices: this.fb.array([
        this.fb.control(''),
        this.fb.control('')
      ])
    })
  }

  get formChoices(){
    return this.createPollForm.get('choices') as FormArray;
  }

  addChoice(){
    this.formChoices.push(this.fb.control(''));
  }

  submitForm(){
    const poll  = this.createPollForm.value;
    this.creatingPoll = true;
    this.http.post('/api/poll/create', poll)
      .subscribe((res: any) => {
        this.response = res;
        this.creatingPoll = false;
        this.createPollForm.reset();
      }, err => {
        this.creatingPoll = false;
        this.response = err.error;
      })
  }

  removeChoice(i){
    this.formChoices.removeAt(i)
  }

  placeholderText(index){
    return `Choice ${index + 1}`;
  }
}
