import { Component, OnInit, Input } from '@angular/core';
import { Chart } from 'angular-highcharts';

@Component({
  selector: 'app-pie-chart',
  template: `
  <div [chart]="chart"></div>
`,
  styleUrls: ['./pie-chart.component.css']
})
export class PieChartComponent implements OnInit {

  @Input() votes;
  pollVotes: any;

  chart: any

  constructor() { }

  ngOnInit() {
    const idk = {};
    for (const vote of this.votes){
      const choice = vote.choice['user choice']
      if (!idk[choice]) {
        idk[choice] = 1;
      } else{
        idk[choice]++;
      }
    }
    this.pollVotes = Object.keys(idk).reduce((preValue, curValue) => {
      return preValue.concat({name: curValue, y: idk[curValue]});
    }, []);


    this.chart = new Chart({
      chart: {
        type: 'pie'
      },
      title: {
        text: 'Votes'
      },
      credits: {
        enabled: false
      },
      series: [
        {
          type: 'pie',
          name: 'Votes',
          data: this.pollVotes
        }
      ]
    });
  }

}
