import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AuthService } from '../Auth/Auth.Service';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';
@Component({
  selector: 'app-poll',
  templateUrl: './poll.component.html',
  styleUrls: ['./poll.component.css']
})
export class PollComponent implements OnInit {

  @Input() poll: any;
  @Output() pollToRemoveId: EventEmitter<number> = new EventEmitter<number>();
  errorResponse: string;
  user: any;
  didVote = false;
  constructor(private authService: AuthService, private http: HttpClient, private router: Router) { }

  ngOnInit() {
    this.authService.userSubject.subscribe(user =>{
      this.user = user;
      if (user.username) {
        this.didVote = !!this.poll.votes.find(vote => vote.username === user.username)
      } else {
        this.didVote = false;
      }
    });

  }

  onSubmit(f: any) {
    if (!this.user.username) {
      this.router.navigate(['/signin']);
    }
    if (!f.option) { return; }
    const {name} = this.poll.choices.find(choice => choice.id === f.option);
    this.http.post(`/api/votes/vote/${this.poll.id}`, {choiceId: Number(f.option)})
      .subscribe(res => {
        const userVote = {username: this.user.username, choice: {choice_id: Number(f.option), 'user choice': name }};
        this.poll = {...this.poll, votes: [...this.poll.votes, userVote]};
        this.didVote = true;
      }, err => {
        this.errorResponse = err.error.message;
      });
  }

  deletePoll() {
    const confirm = window.confirm('Are you sure you want to delete this poll');
    if (!confirm) { return; }
    const pollId = this.poll.id;
    this.http.delete(`/api/poll/remove/${pollId}`)
      .subscribe(res => {
        this.pollToRemoveId.emit(this.poll.id);
      }, err =>{
        this.errorResponse = 'Poll Already deleted';
      });
  }
}
