import {NgModule} from '@angular/core'
import {CommonModule} from '@angular/common'
import {RouterModule, Routes} from '@angular/router'
import {SigninComponent} from './Signin/Signin.component'
import {SignupComponent} from './Signup/Signup.component'
import {ReactiveFormsModule} from '@angular/forms'
import { AuthService } from './Auth.Service';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { AuthInterceptor } from './Auth.interceptor';
import { SigninAndOutGaurd } from './SigninAndOut.guard';

const routes: Routes = [
  {path: 'signin', pathMatch: 'full', component: SigninComponent, canActivate: [SigninAndOutGaurd]},
  {path: 'signup', pathMatch: 'full', component: SignupComponent, canActivate: [SigninAndOutGaurd]}
]

@NgModule({
  declarations: [SigninComponent,
                 SignupComponent
                ],
  providers: [
    AuthService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true
    },
    SigninAndOutGaurd
  ],
  imports: [CommonModule,
            RouterModule.forChild(routes),
            ReactiveFormsModule
  ]
})
export class AuthModule{}
